.resume-link{
    position: relative;
    &::after{
        position: absolute;
        bottom: -100%;
        left: 0;
        width: 100%;
        content: "Click to View";
    }

    .resume-thumbnail{
        width: 50%;
        box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.767);
    }
}